
main {
	
	#calculator_ups {
		width: 800px;
		fieldset {
				input {
					height: 38px;
					padding: 8px 10px;
					box-sizing: border-box;
				}
				input[type="number"] {
					-moz-appearance: textfield;
					&::-webkit-inner-spin-button, 
					&::-webkit-outer-spin-button { 
				  		-webkit-appearance: none; 
				  		margin: 0; 
					}
				}		
			.textrow > .text-left {
				text-align: left;
				display: inline-block;
				h3 {
					font-size: 16px;
				}
			}
			.dropdown, .datainputs {
				display: inline-block;
			}
		}

		#fieldset_standard {
			
			input[type="number"] {
				width: 50%;
			}

			input[type="text"] {
				width: 100%;
			}

			.dropdown {
				width: 33.3333%;
				position: relative;
				span {
					position: absolute;
					right: 0;
					top: 5px;
					width: 20px;
					height: 20px;
					background: gold;
				}
			}

			.datainputs {
				width: 66.6666%;
			}

			.textrow > .text-left {
				width: 33.33333%;
			}
		}

		#fieldset_amplifiers {

			input[type="text"] {
				width: 100%;
			}
			input[type="number"] {
				width: 50%;
			}
			
			.amplifier_name, .dropdown {
				width: 23.5%;
				display: inline-block;
			}
			.datainputs {
				width: 47%; 	
			}
			.textrow > .text-left {
				width: 23.5%;
				&:first-of-type {
					width: 6%;
				}
			}
		}
		#field_otheritems {
			
		}
		#calculate {
			display: block;
			padding: 10px 14px;
			color: white;
			border: none;
			background: #ee5c42;
			font-size: 18px;
			margin: 10px auto;
		}
		.result, .error {
			text-align: center;
		}
	}
	.button_addition {
		display: inline-block;
		width: 6%;
	}
	button.add_amplifier, button.add_otheritem, button.remove_amplifier, button.remove_otheritem {
		background: #49E20E;
		width: 30px;
		height: 30px;
		border: none;
		border-radius: 50%;
		color: white;
		font-weight: 600;
		padding: 0;
		line-height: 27px;
		font-size: 28px;
		top: 4px;
		position: relative;
		&:focus {
			outline: none;
		}
	}
	button.remove_amplifier, button.remove_otheritem {
		background: red;
	}
	button.remove_otheritem {
		margin-bottom: 10px;
	}
}